<template lang="html">
  <div class="bind-bankcard-yzm">
    <!-- 头部 -->
    <default-head header="填写验证码"></default-head>
    <light-title text="请输入13812345678收到的短信验证码"></light-title>
    <form class="form-box">
      <div class="yzm clearfix">
        <input type="number" name="" value="" class="fl" placeholder="短信验证码">
        <span class="fl">30秒后重发</span>
      </div>
      <sub-button text="下一步" marginTop="1rem" @click.native="$router.push('/bindbankcardresult')"></sub-button>
    </form>
  </div>
</template>

<script>
import defaultHead from '@/components/head/defaultHead'
import lightTitle from '@/components/common/lightTitle'
import formLine from '@/components/common/formLine'
import formLineLight from '@/components/common/formLineLight'
import subButton from '@/components/common/subButton'

export default {
  data () {
    return {
    }
  },
  components: { defaultHead, lightTitle, formLine, formLineLight, subButton }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.bind-bankcard-yzm {
  padding-top: .88rem;
  .form-box {
    .yzm {
      padding: 0 .22rem;
      input, span {
        height: .88rem;
        line-height: .88rem;
        font-size: .32rem;
      }
      input {
        width: 3.7rem;
        border-radius: .05rem;
        padding-left: .95rem;
        border: 1px solid @lightColor;
        color: @tablebgColor;
        &::placeholder {
          color: @tablebgColor;
        }
      }
      span {
        width: 2.74rem;
        margin-left: .6rem;
        background: @lightColor;
        color: @whiteColor;
        text-align: center;
        border-radius: .05rem;
      }
    }
  }
}

</style>



